import React from "react";
import DetailPresenter from "./DetailPresenter";
import { moviesApi, tvApi } from "api";

// ì»¨í…Œì´ë„ˆ ì»´í¬ë„ŒíŠ¸

export default class extends React.Component {
  // isMovieë¥¼ constructor í•¨ìˆ˜ ì•ˆì— ë§Œë“  ì´ìœ 
  // ì „ì—­ìœ¼ë¡œ ì‚¬ìš©í•˜ê¸° ìœ„í•´ì„œì¸ë“¯ í•˜ë‹¤.

  // constructor í•¨ìˆ˜ ì•ˆì—ëŠ” ë¬´ì¡°ê±´ super()ê°€ ìˆì–´ì•¼ í•˜ë©°,
  // ì´ëŠ” thisë¥¼ ì´ˆê¸°í™” í•˜ê¸° ìœ„í•¨ì´ë‹¤. ( ìƒì„±ì í•¨ìˆ˜ ì´ˆê¸°í™” )
  // super(props)ë¥¼ ì‚¬ìš©í•˜ëŠ” ì´ìœ ëŠ” constructor í•¨ìˆ˜ ì•ˆì—ì„œ
  // this.props ë¥¼ ì‚¬ìš©í•˜ê¸° ìœ„í•¨ì´ê³ , ë‹¤ë¥¸ê³³ì—ì„œ ì‚¬ìš©í•˜ë ¤ë©´
  // êµ³ì´ ë­”ê°€ë¥¼ ë„£ì–´ì¤„ í•„ìš”ê°€ ì—†ë‹¤. ( ë¦¬ì•¡íŠ¸ê°€ ìë™ìœ¼ë¡œ ì„¸íŒ…í•´ì£¼ê¸° ë•Œë¬¸ )
  constructor(props) {
    super(props);
    const {
      location: { pathname }
    } = props;

    // idê°’ì„ ê°€ì ¸ì™€ì„œ í•˜ë‚˜ì˜ ê²°ê³¼ê°’ë§Œ ë³´ì—¬ì¤€ë‹¤.
    this.state = {
      result: null,
      error: "",
      loading: true,
      isMovie: pathname.includes("/movie/")
    };
  }

  async componentDidMount() {
    const {
      match: {
        params: { id }
      },
      history: { push }
    } = this.props;
    const { isMovie } = this.state;

    // parseInt : string >> number
    // id ê°€ numberê°€ ì•„ë‹ˆë©´ home ìœ¼ë¡œ return
    const parseId = parseInt(id);
    if (isNaN(parseId)) {
      return push("/");
    }

    let result = null;
    try {
      // const a = "dd" === (a = "dd")
      if (isMovie) {
        ({ data: result } = await moviesApi.movieDetail(parseId));
      } else {
        ({ data: result } = await tvApi.tvDetail(parseId));
      }
    } catch {
      this.setState({ error: "can't find anything " });
    } finally {
      // resultê°€ tvë˜ movieë˜ ê°’ì„ ë°›ì•„ì„œ stateì— ê¸°ë¡í•¨
      this.setState({ loading: false, result });
    }
  }

  render() {
    const { result, error, loading, isMovie } = this.state;
    return (
      <DetailPresenter
        result={result}
        error={error}
        loading={loading}
        isMovie={isMovie}
      />
    );
  }
}
