import React from "react";
import TVPresenter from "./TVPresenter";
import { tvApi } from "api";

export default class extends React.Component{
  state = {
    topRated: null,
    popular: null,
    airingToday: null,
    tvDetail: null,
    error: null,
    loading: true
  }

  // componentWillMount = async() => {}
  // ë™ì¼
  async componentDidMount() {
    try{
      const {data:
        {results:topRated}
      } = await tvApi.topRated();
      const {data:
        {results:popular}
      } = await tvApi.popular();
      const {data:
        {results:airingToday}
      } = await tvApi.airingToday();
      this.setState({
        topRated,
        popular,
        airingToday
      })
    } catch {
      this.setState({
        error: "Can't find TV information."
      })
    } finally {
      this.setState({
        loading: false
      })
    }
  }

  render() {
    const { topRated, popular, airingToday, tvDetail, error, loading } = this.state
    return (
      <TVPresenter 
        topRated={topRated}
        popular={popular}
        airingToday={airingToday}
        tvDetail={tvDetail}
        error={error}
        loading={loading}
      />
    );
  }
}