import React from "react"; 
import SearchPresenter from "./SearchPresenter";
import { moviesApi, tvApi } from "api";

export default class extends React.Component{
  state = {
    // ê²€ìƒ‰ ê²°ê³¼
    movieResults: null,
    tvResults: null,
    // ê²€ìƒ‰ ë‚´ìš©
    searchTerm: "",
    // ê²€ìƒ‰ í•˜ë©´ true
    loading: false,
    // error ìˆìœ¼ë©´ ê°’ë„£ê³ , ì—†ìœ¼ë©´ null ìœ ì§€
    error: ""
  };

  updateTerm = event => {
    const { target: {value} } = event;
    this.setState({
      searchTerm: value
    })
  }

  // #1 í…ŒìŠ¤íŠ¸ìš©
  // SearchPresenterì—ì„œ handleSebmit í˜¸ì¶œ
  // componentDidMountì¼ë•Œ handleSubmitì„ í˜¸ì¶œ
  // componentDidMount() {
  //   this.handleSubmit();
  // };

  // #2
  // í˜¸ì¶œí–ˆì„ë•Œ searchTermì˜ ê¸°ë³¸ê°’ì€ ""
  // í¼ì— ì…ë ¥í•œ ë°ì´í„°ë¥¼ Submit
  handleSubmit = event => {
    event.preventDefault();
    const { searchTerm } = this.state;
    if(searchTerm !== ""){
      this.searchByTerm(searchTerm);
    }
  };

  // #3
  // searchByTermì´ í˜¸ì¶œë˜ê³ , mocieResultì™€ tvResultë¥¼ ì°¾ëŠ”ë‹¤.
  // ê²€ìƒ‰ ê²°ê³¼
  //searchByTerm = term => {
  //ì•„ë˜ì™€ ë™ì¼
  searchByTerm = async() => {
    const { searchTerm } = this.state;
    try {
      const {
        data: { results: movieResults }
      } = await moviesApi.search(searchTerm);
      const {
        data: { results: tvResults }
      } = await tvApi.search(searchTerm);
      this.setState({
        movieResults,
        tvResults
      })
    } catch {
      this.setState({ error: "Can't find results "})
    } finally {
      this.setState({ loading: false })
    }
  };

  render() {
    const { 
      movieResults, 
      tvResults, 
      searchTerm, 
      loading, 
      error }  
      = this.state;
    
    return (
      <SearchPresenter 
        movieResults={movieResults}
        tvResults={tvResults}
        searchTerm={searchTerm}
        loading={loading}
        error={error}
        handleSubmit={this.handleSubmit}
        updateTerm={this.updateTerm}
      />
    );
  }
}