export default class extends React.Component{
  state = {
    nowPlaying: null,
    upcoming: null,
    popular: null,
    error: null,
    loading: true
  }

  // 2option
  // 1. ì „ì²´ API ìš”ì²­ì„ í•œêº¼ë²ˆì— í•  ìˆ˜ ìˆê³ ,
  // 2. ê°ê°ì˜ ìš”ì²­ì„ ë¶„ë¦¬ëœ í•¨ìˆ˜ë¡œ ë§Œë“¤ì–´ì„œ ë”°ë¡œ ìš”ì²­ ê°€ëŠ¥
  // ê·¸ë ‡ê²Œ í¬ì§€ ì•Šë‹¤ë©´ componentDidmount ì•ˆì—ì„œ ì „ë¶€ í•´ê²°
  async componentDidMount() {
    // async
    // await
    // ë­”ê°€ë¥¼ ëë‚¼ë•Œê¹Œì§€ ê¸°ë‹¤ë¦¬ëŠ” í•¨ìˆ˜
    try{
      const {
        data: {results: nowPlaying}
      } = await moviesApi.nowPlaying();
      const {
        data: {results: upcoming}
      } = await moviesApi.upcoming();
      const {
        data: {results: popular}
      } = await moviesApi.popular();
      this.setState({
        // nowPlaying : nowPlaying 
        // ë™ì¼í•˜ê²Œ ì¸ì‹
        nowPlaying,
        upcoming,
        popular
      })
    } catch {
        this.setState({
          error: "Can't find movies imformation."
        })
    } finally {
        this.setState({
          loading: false
        })
    }
  }

  // HomePresenterë¡œ ê°€ëŠ” ëª¨ë“  ìŠ¤í…Œì´íŠ¸ ê°’ì„ ë Œë”ë§
  // Presenter(ë³´ì—¬ì£¼ëŠ”)ê²ƒ ì—†ì´ ë Œë”ë§ë§Œ
  render() {
    const { nowPlaying, upcoming, popular, error, loading } = this.state;
    return (
      <HomePresenter 
        nowPlaying={nowPlaying} 
        upcoming={upcoming}
        popular={popular}
        error={error}
        loading={loading}
      />
    );
  }
}